<template>
<div>
  <h1>维度: {{point.latitude}}</h1>
  <h1>经度: {{point.longitude}}</h1>
</div>
</template>

<script>
import AV from './api/AV'

export default {
  name: 'app',
  data() {
    return {
      point: {}
    }
  },
  mounted() {
    AV.GeoPoint.current().then(point => this.point = point)
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



